.main-container {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.main-tabs {
    background-color: #ffffff;
    margin: 0;
    padding: 0;
    display: flex;
    height: 50px;
    border-bottom: 0.5px solid #e7e7e7;
}

.main-tabs > li {
    list-style: none;
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-tabs > li > a {
    text-decoration: none;
    color: #000000;
    font-size: 8pt;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
}

.profile-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.5px solid #e7e7e7;
}

.profile-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.profile-image {
    width:50px;
    height:50px;
    border-radius:50%;
    margin: 10px 15px;
}

.profile-nickname {
    font-weight: bold;
    font-size: 12pt;
}

.settings-btn:hover {
    cursor: pointer;
}
.loading {
    position: absolute;
    width: 100wv;
    height: 100wh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.working-container {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
#working {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color:#dcdcdc;
  flex: 1 1;
}

.label {
  /* font-family: 'Nanum Gothic', sans-serif; */
  color: #353645;
  display: flex;
  align-items: center;
  width: calc(100% - 16px);
  margin: 8px 24px;
}

.labelTitle {
  text-align: right;
  margin-right: 16px;
  flex: 0 1 60px;
  font-size: 11pt;
  height: 100%;
}

.working-hour {
  font-size: 16px;
  flex: 1 1;
  /* text-align: right; */
}

.time {
  font-size: 15px;
  flex: 1 1;
}

.txtWrap {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  text-align: center;
}

.pay {
  text-align: right;
  font-size: 16px;
}

.won {
  margin-left: 3px;
}

.callnumber {
  /* width: 140px; */
  padding: 0 5px;
  text-align: right;
  font-size: 16px;
}

.detail-container {
  flex: 1 1;
}

.textarea {
  flex: 1 1;
  display: flex;
  height: 100%;
}

.input-text {
  background-color: #ffffff;
  padding: 8px;
  /* box-shadow: 0 2px 8px #d7d7d7; */
  border-radius: 16px;
  display: flex;
  flex: 1 1;
  align-items: center;
}

.working-text {
  flex: 1 1;
  display: flex;
}

.working-detail {
  flex: 0 1 50px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.more-details {
  /* border: 1px solid black; */
  border: 0;
  padding: 8px;
  border-radius: 16px;
  font-size: 10pt;
  flex: 1 1;
  resize: none;
}

input:focus,textarea:focus, select:focus {
  outline: none;
}

input {
  border: 0;
  border-bottom: 0.5px solid #e7e7e7;
}

.send-footer {
  flex: 0 1 50px;
  border-top: 0.5px solid #d7d7d7;
}

.paylabel {
  height: 100%;
}

.pay-select {
  background-image:
  -webkit-linear-gradient(45deg, transparent 50%, gray 50%),
  -webkit-linear-gradient(315deg, gray 50%, transparent 50%);
  background-image:
  linear-gradient(45deg, transparent 50%, gray 50%),
  linear-gradient(135deg, gray 50%, transparent 50%);
  /* linear-gradient(to right, #ccc, #ccc); */
  background-position:
    calc(100% - 11px) calc(1em),
    calc(100% - 6px) calc(1em),
    calc(100% - 2.5em) 0.5em;
  background-size:
    5px 5px,
    5px 5px,
    1px 1.5em;
  background-repeat: no-repeat;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  border: 0;
  height: 30px;
  border-radius: 16px;
  font-size: 10pt;
  padding: 0 8px;

}
.wrap {
    margin: auto;
}

ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.typearea{
    width: 100%;
    height: 100px;
    background-color: #ffffff;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

p{
    margin: 0;
    font-family: 'Nanum Gothic', sans-serif;
}

#lowestpay{
    padding: 15px 25px 0px 30px;
}

#fromtoul{
    padding: 15px 30px;
    margin: auto;
    text-align: center;
}

#fromtoul li{
    float: left;
    padding: 5px;
}

.formarea{
    width: 100%;
    height: 230px;
    background-color: #DCDCDC;
}

.formarea a{
    text-decoration: none;
    color: black;
}

#formul{
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#formul li{
    padding: 15px 5px 0px 15px;
    display: inline-block;
    width: 300px;
    font-family: 'Nanum Gothic', sans-serif;
}

li input{
    margin: 0 5px 0 0;
}

.title{
    height: auto;
    width: 110px;
    float: left;
    font-weight: 600;
}

.formarea input{
    border-radius: 5px;
}

.formarea input:focus { 
    outline: none;
    border-color: #ffffff;
    box-shadow: 0 0 0 #ffffff;
}

.calculbtn{
    width: 100px;
    height: 40px;
    background-color: #353646;
    margin: 30px auto;
    border-radius: 5px;
}

.calculbtn p{
    padding: 11px 18px 0 20px;
    color: #ffffff;
}

.resultarea{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#resultul{
    margin: 10px 30px 30px 30px;
    padding: 5px 2px 0 18px;
    text-align: center;
}

#resultul li{
    margin: 0 5px 0 0;
    display: inline-block;
}

#resultpay{
    font-size: 1.2em;
    color: #353646;
}

#warn{
    text-align: center;
    font-size: 0.85em;
}
.settings {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1;
}

.settings-header {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 0.5px solid #e7e7e7;
}

.back-button {
    margin: 0px 15px;
}

.back-button:hover {
    cursor: pointer;
}

.settings-header-title {
    font-weight: bold;
}

.settings-list {
    flex: 1 1;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
    padding-top: 15px;
}

.settings-list > .settings-list-item {
    height: 50px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-top: 0.5px solid #e7e7e7;
    border-bottom: 0.5px solid #e7e7e7;
}

.settings-list > .settings-list-item:hover {
    cursor: pointer;
}

.settings-list > .settings-list-item > .settings-list-item-title {
    margin: 0px 15px;
}

.settings-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 45px;
    border-top: 0.5px solid #e7e7e7;
}

.logout-button {
    margin: 0px 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10pt;
}

.logout-button:hover {
    cursor: pointer;
}
.settings-modal {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    box-shadow: 0 8px 16px #d7d7d7;
    border: 0.5px solid #e7e7e7;
    border-radius: 8px;
    padding: 8px;
}   

.modal-btn-exit {
    position: absolute;
    right: 8px;
    top: 8px;
}

.modal-btn-exit:hover {
    cursor: pointer;
}
